@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: auto;
  margin: 0 auto;
}


.post{
    width: 30%;
    margin: 10px;
    padding: 15px;
    color: #000;
    border: 1px solid cadetblue;
}
.post h2{
    font-size: 30px;
    font-family: 'Roboto', Arial, Helvetica, sans-serif;
    color: darkgreen;
}
.post p{
    padding: 10px;
    margin: 5px;
    text-align: right;
}.post img{
    width: 30px;
    padding: 10px;
    border: 1px solid cadetblue;
}
.post h3{
    color: darkcyan;
}
.post button{
    width: 70px;
    height: 70px;
    _background-color: rgb(212, 248, 233);
    _border: 1px solid rgb(3, 211, 72);
    border: 0;
    margin: 10px;
    padding: 10px;
    font-size: 20px;
    font-weight: 600;
    border-radius: 50%;
    outline: none;
}
.green{
    color: darkcyan;
}
.green:hover{
    background-color: darkseagreen;
    color: aliceblue;
}
.red{
    color: crimson;
}
.red:hover{
    background-color: rgb(247, 133, 133);
    color: #fff;
}
.post-list{
    width: auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    _flex-direction: column;
    margin: 10px auto;
    border: 1px solid transparent;
}
.title{
    font-size: 35px;
    font-weight: 700;
    color: cadetblue;
    margin: 30px 0;
    padding-bottom: 15px;
    border-bottom: 1px solid rgba(0, 128, 0, 0.13);
}
